@font-face {
  font-family: 'Arcade';
  src: url(/maxblaushild.me/static/media/ARCADE.f841b176.TTF);
}

body {
  margin: 0;
  padding: 0;
  font-family: "Arcade";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  width: 20vmin;
  border-radius: 50%;
  object-fit: cover;
}

.App-img {
  background-color: white;
  height: 4vmin;
  width: 4vmin;
  display: inline-block;
  margin: 20px;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-text {
  background: white;
  width: 100%;
  max-width: 600px;
  margin-bottom: 0;
  margin-top: 20px;
}
